.header {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 48px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  color: #fff;
  background-color: #f0ebeb;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);

  width: 100%;
}
.themeDark {
  color: #9f9f9f;
  background-color: #2b1b36;
}

.leftNav {
  width: 100%;
}
.rightNav {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
}

.button {
  display: inline-block;
  width: 48px;
  height: 48px;
  border: 0;
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.button:hover {
  opacity: 1;
}

.label {
  font-size: 1.6em;
  position: relative;
  top: -1px;
}

.input {
  display: inline-block;
  width: 100%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  padding-left: 8px;
  padding-right: 4px;
}

.input::placeholder {
  font: inherit;
  font-size: 18px;
}
